@import "./_reset.scss";

#root {
  position: relative;
  height: 100vh;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pokedex {
  aspect-ratio: 4/3;
  max-width: 1280px;
  max-height: 960px;
  width: 100%;
  display: flex;
  font-size: 33.33%;
  line-height: 1.4;
  background: url(./pokedex.png) no-repeat center / cover;
  &.focusMain {
    transform: scale(2);
    margin-left: 50%;
    margin-right: -50%;
    margin-top: -8rem;
  }
  &.focusCover {
    transform: scale(2);
    margin-left: -50%;
    margin-right: 50%;
    margin-top: -8rem;
  }
}

.main {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 0.5rem 2.1rem 1.94rem 1.9rem;
  .lights {
    height: 3.25rem;
    display: flex;
    align-items: flex-start;
    gap: 0.4rem;
    .light {
      border-radius: 50%;
      width: 1rem;
      height: 1rem;
      &.emerald {
        margin-top: 0.15rem;
        width: 3.1rem;
        height: 3.1rem;
        animation: blink 1s cubic-bezier(0, 1, 0, 1) 0s infinite;
      }
      &.red {
        background: radial-gradient(ellipse at 40% 30%, red, #000000aa);
      }
      &.yellow {
        margin-left: 0.03rem;
        background: radial-gradient(ellipse at 40% 30%, yellow, #000000aa);
      }
      &.green {
        background: radial-gradient(ellipse at 40% 30%, lightgreen, #000000aa);
        margin-left: 0.07rem;
      }
    }
    @keyframes blink {
      from {
        background: radial-gradient(ellipse at 40% 30%, #0099ff66, #00000066);
      }
      50% {
        background: radial-gradient(ellipse at 40% 30%, #0099ffaa, #000000aa);
      }
      to {
        background: radial-gradient(ellipse at 40% 30%, #0099ff66, #00000066);
      }
    }
  }
  .mainVisual {
    height: 7.97rem;
    flex-shrink: 0;
    margin: 4.3rem 1.24rem 3.1rem 1.25rem;
    border-radius: 0.55rem;
    overflow: hidden;
    .gif {
      height: 100%;
      padding: 2.8rem;
      img {
        height: 100%;
        object-fit: contain;
      }
    }
    .pokemonListWrap {
      position: relative;
      height: 100%;
      display: flex;
      flex-direction: column;
      background-color: #fff;
      .pokemonListHeader {
        display: flex;
        padding-top: 6px;
        background-color: #000;
        p {
          color: #fff;
        }
        .no {
          width: 60px;
          text-align: center;
        }
        .name {
          flex: 1;
        }
      }
      .pokemonList {
        flex: 1;
        height: calc(100% - 20px);
        padding: 6px 0;
        overflow-y: scroll;
        &::-webkit-scrollbar {
          display: none;
        }
        li {
          position: relative;
          display: flex;
          cursor: pointer;
          .no {
            width: 60px;
            padding-left: 20px;
          }
          .name {
            flex: 1;
          }
          &.hover {
            &::before {
              content: "▶︎";
              display: block;
              position: absolute;
              left: 0.05rem;
              top: -0.05rem;
              font-size: 0.4rem;
            }
          }
          &.active {
            background-color: #00000055;
            * {
              color: #fff;
            }
          }
        }
      }
      .img {
        position: absolute;
        right: 0;
        top: 28.4px;
        background-color: #fff;
        border: solid #000;
        border-width: 0 0 0.1rem 0.1rem;
        img {
          width: 2.4rem;
          height: 2.4rem;
          object-fit: cover;
        }
      }
    }
  }
  .utils {
    flex: 1;
    display: flex;
    align-items: center;
    gap: 0.4rem;
    .assets {
      flex: 1;
      position: relative;
      padding-left: 2.3rem;
      height: 100%;
      > * {
        position: absolute;
      }
      .circleAsset {
        left: 0;
        top: 0;
        width: 2.1rem;
        height: 2.1rem;
        border-radius: 50%;
      }
      button {
        width: 2.5rem;
        height: 0.6rem;
        top: 0.7rem;
        border-radius: 9rem;
        font-size: 0.4rem;
        &.abort {
          left: 3rem;
        }
        &.select {
          left: 6.4rem;
        }
      }
      .nameVisual {
        bottom: 0;
        height: 2.9rem;
        width: 5.9rem;
        border-radius: 0.3rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
    }
    .crossBtns {
      position: relative;
      flex-shrink: 0;
      width: 4.4rem;
      height: 4.4rem;
      top: -0.3rem;
      button {
        width: calc(50% - 0.525rem);
        height: 1.25rem;
        position: absolute;
      }
      .x {
        button {
          top: calc(50% - 0.625rem);
          &:nth-child(1) {
            left: 0;
          }
          &:nth-child(2) {
            right: 0;
          }
        }
      }
      .y {
        button {
          transform: rotate(90deg);
          left: 1.5125rem;
          &:nth-child(1) {
            top: 0.2775rem;
          }
          &:nth-child(2) {
            bottom: 0.2775rem;
          }
        }
      }
    }
  }
}
.cover {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 8.7rem 2.8rem 2.45rem 3.5rem;
  .detailVisual {
    height: 4.5rem;
    border-radius: 0.2rem;
    padding: 0 0.4rem;
    display: flex;
    align-items: center;
    text-align: center;
    color: #fff;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000,
      1px 1px 0 #000;
  }
  .stat {
    margin: 0.95rem 0 0.6rem 0.2rem;
    height: 3.4rem;
    border-radius: 0.4rem;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-auto-flow: column;
    align-items: center;
    justify-items: center;
    font-size: 0.45rem;
  }
  .assets {
    position: relative;
    flex: 1;
    margin-bottom: 1.3rem;
    .horAssets {
      position: absolute;
      right: 0;
      top: 0;
      display: flex;
      gap: 0.1rem;
      > i {
        width: 2.2rem;
        height: 0.6rem;
        border-radius: 9rem;
      }
    }
    .btnAssets {
      position: absolute;
      left: 0;
      bottom: 0.3rem;
      display: flex;
      border-radius: 0.4rem;
      > i {
        width: 2.5rem;
        height: 1.8rem;
      }
    }
    .circleAsset {
      position: absolute;
      right: 0;
      bottom: 0;
      display: block;
      width: 1.4rem;
      height: 1.4rem;
      border-radius: 50%;
    }
  }
  .summaryWrap {
    height: 1.8rem;
    display: flex;
    gap: 0.87rem;
    > div {
      border-radius: 0.2rem;
      flex: 1;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 0.04rem;
      p {
        color: #fff;
        text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000,
          1px 1px 0 #000;
      }
      [class$="typeVisual"],
      &.numVisual {
        background: conic-gradient(
          #ceb344 8%,
          #d09949 8% 15%,
          #ce684c 15% 19%,
          #c9606e 19% 22%,
          #b975a9 22% 25%,
          #9f5b89 25% 28%,
          #694d94 28% 31%,
          #63537a 31% 35%,
          #596677 35% 42%,
          #5f708d 42% 50%,
          #4887be 50% 58%,
          #3d9cc4 58% 65%,
          #428dc4 65% 69%,
          #457cbb 69% 72%,
          #4095b9 72% 75%,
          #61b7c2 75% 78%,
          #56c267 78% 81%,
          #a5b65b 81% 85%,
          #aaaf80 85% 92%,
          #b69867 0
        );
      }
      &.typeVisual {
        &.normal {
          background-color: #949495;
        }
        &.fire {
          background-color: #e56c3e;
        }
        &.water {
          background-color: #5185c5;
        }
        &.grass {
          background-color: #66a945;
        }
        &.electric {
          background-color: #f6d851;
        }
        &.ice {
          background-color: #6dd3f5;
        }
        &.fighting {
          background-color: #e09c40;
        }
        &.poison {
          background-color: #735198;
        }
        &.ground {
          background-color: #9c7743;
        }
        &.flying {
          background-color: #a2c3e7;
        }
        &.psychic {
          background-color: #dd6b7b;
        }
        &.bug {
          background-color: #9fa244;
        }
        &.rock {
          background-color: #bfb889;
        }
        &.shadow {
          background-color: #684870;
        }
        &.ghost {
          background-color: #fff;
        }
        &.dragon {
          background-color: #535ca8;
        }
        &.dark {
          background-color: #4c4948;
        }
        &.steel {
          background-color: #69a9c7;
        }
        &.fairy {
          background-color: #dab4d4;
        }
      }
    }
  }
}
